$container: 1280px;
$footerMobile: 60px;
$asideAlunoWidth: 315px;

$headerDesktop: 80px;

$colors: ( blue: #162133, blue2: #123C60, blue_light: #388FF4, blue_light2: #007EDB,  blue_light_hover: #0062c5, gray: #717171, gray_light: #F2F2F2, light: #EDEDED );

// bc211b
// Fontes
// ==========================================================================
@mixin lato-font {
  font-family: 'Lato', sans-serif;
  font-weight: normal; }

// transitions
// ==========================================================================
@mixin transition-linear {
  transition: all .3s linear; }

@mixin transition-ease {
  transition: all .3s ease; }

// Keyframes
// ==========================================================================
@keyframes pointing {
  from {
    background-position: 0 0; }
  to {
    background-position: 5px 0; } }

// Mixins
// ==========================================================================
@mixin placeholder {
  &::-webkit-input-placeholder {
    @content; }

  &:-moz-placeholder {
    @content; }

  &::-moz-placeholder {
    @content; }

  &:-ms-input-placeholder {
    @content; } }
